import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import f from './styles.module.scss'
import RepairBoxLogo from '../../img/icons/RepairBoxLogo'

class Footer extends Component {
  render() {
    return (
      <footer className={f.footer}>
      <div className={f.container}>
          <div className={f.footerInfo}>
              <div className={f.logo}>
                  <Link to="/apple-service" className={f.title}><RepairBoxLogo className={f.logoIcon}/></Link>
              </div>
              <div className={f.repair}>
                  <h3 className={f.title}></h3>
                  <ul className={f.list}>
                      <li><Link to='/iphone' title=' iPhone'> iPhone</Link></li>
                      <li><Link to='/ipad' title=' iPad'> iPad</Link></li>
                      <li> MacBook</li>
                      <li> iMac</li>
                      <li> Samsung</li>
                      <li><Link to='/other-devices'>  </Link></li>
                  </ul>
              </div>
              <div className={f.sale}>
                  <h3 className={f.title}></h3>
                  <ul className={f.list}>
                      {/* <li><Link to='/iphone-sell'>iPhone</Link></li> */}
                      <li></li>
                  </ul>
              </div>
              <div className={f.phones}>
                  <p className={f.phone}>+7(985) 070-80-80</p>
                  <h3 className={f.title}> -</h3>
                  <ul className={f.list}>
                      <li>.</li>
                      <li>+7(985) 070-80-80</li>
                      <li >. </li>
                      <li>+7(916) 611-55-50</li>
                      <li> :</li>
                      <li>- 10:00 - 18:00</li>
                  </ul>
              </div>
          </div>
          <div className={f.footerSocial}>
              <a className={f.vk} href="https://vk.com/club100386203" > </a>
          </div>
      </div>
  </footer>
    );
  }
  }

  export default Footer


