import React, {Component} from 'react';
import CloseIcon from '../../img/icons/CloseIcon'
import cx from 'classnames'
import Link from 'gatsby-link'
import {withRouter} from 'react-router'
import s from './styles.module.scss'

class Menu extends Component {
    constructor() {
        super()
        this.state = {
            active: '',
            activeCategory: ''
        }
    }

    componentDidMount () {
        document.addEventListener('click', this.handleOutsideClick);
    }

    handleOutsideClick = (event) => {
        const item = document.querySelector(`.${s.active}`)
        if (item) {
            if (!item.contains(event.target)) {
                this.setState({
                    active: ''
                })
                document.removeEventListener('click', this.handleOutsideClick);
            }
        }
    }

    setActiveMenu = (event) => {
        const target = event.target
        this.setState({active: target.textContent})
        this.props.toggleMenu()
    }

    render() {
        console.log(this.props.location.pathname);

        return (
            <nav className={this.props.visibility ? s.menu : s.hidden}>
                <CloseIcon className={s.closeIcon} onClick={this.props.toggleMenu}/>
                <div className={s.wrapper}>
                    <div className={cx(`${s.itemGroup} ${this.state.active === '' ? `${s.active}` : ''}`)} onClick={this.getActiveCategory}>
                        <h3 className={cx(s.title)} onClick={this.setActiveMenu}></h3>
                        <ul className={cx(`${s.list} ${this.state.active === '' ? `${s.active}` : ''}`)}>
                            <li className={s.item} onClick={this.setActiveMenu}><Link to='/iphone' title='iPhone'> iPhone</Link></li>
                            <li className={s.item} onClick={this.setActiveMenu}><Link to='/ipad' title='iPad'> iPad</Link></li>
                            <li className={s.item} onClick={this.setActiveMenu}><Link to='/other'>  </Link></li>
                            <li className={s.item} onClick={this.setActiveMenu}><a href=''> Macbook</a></li>
                            <li className={s.item} onClick={this.setActiveMenu}><a href=''> iMac</a></li>
                        </ul>
                    </div>
                    <div className={cx(`${s.itemGroup} ${this.state.active === '' ? `${s.active}` : ''}`)}>
                        <h3 className={s.title} onClick={this.setActiveMenu}></h3>
                        <ul className={`${s.list} ${this.state.active === '' ? `${s.active}` : ''}`}>
                            <li className={s.item} onClick={this.setActiveMenu}><Link to='/iphone-sell'> iPhone</Link></li>
                            <li className={s.item} onClick={this.setActiveMenu}><a href=''> </a></li>
                        </ul>
                    </div>
                    <div className={s.itemGroup}>
                        {this.props.location.pathname === '/' && <h3 className={s.title} onClick={this.setActiveMenu}><a href='#scroll_here'></a></h3>}
                    </div>
                    <div className={s.itemGroup}>
                        {this.props.location.pathname === '/iphone-sell' && <h3 className={s.title} onClick={this.setActiveMenu}><Link to='/basket'></Link></h3>}
                    </div>
                </div>
            </nav>
        )
    }
}

export default withRouter(Menu)



